.signup_page{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    padding-top: 2rem;
}

.form{
    border: 1px solid #000;
    border-radius: 9px;
    padding: 2rem 4rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.form-label{
    color: black;
    font-weight: 500;
}

.form-sec{
    display: flex;
    flex-direction: column;
}

.form-input{
    padding: 0.5rem 1rem;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #000;
    outline: none;
    font-weight: 400;
    font-size: 1rem;
}

.form_btn{
    background-color: #ffbe00;
    padding: 1rem 0;
    border: none;
    border-radius: 9px;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
}

.fogotpass_link{
    color: #000;
    text-transform: capitalize;
}